<script setup lang="ts">
import { ref, onMounted } from 'vue'
import * as d3 from 'd3'

// 图表引用
const signalTrendChart = ref<HTMLElement | null>(null)
const projectActivityChart = ref<HTMLElement | null>(null)

// 模拟数据
const recentSignals = [
  { id: 1, asset: 'SOL', type: 'trend', strength: 85, timestamp: new Date(Date.now() - 1000 * 60 * 30) },
  { id: 2, asset: 'BTC', type: 'momentum', strength: 72, timestamp: new Date(Date.now() - 1000 * 60 * 60 * 2) },
  { id: 3, asset: 'ETH', type: 'trend', strength: 68, timestamp: new Date(Date.now() - 1000 * 60 * 60 * 4) },
  { id: 4, asset: 'ADA', type: 'reversal', strength: 91, timestamp: new Date(Date.now() - 1000 * 60 * 60 * 6) },
  { id: 5, asset: 'DOT', type: 'momentum', strength: 59, timestamp: new Date(Date.now() - 1000 * 60 * 60 * 8) }
]

const hotProjects = [
  { id: 1, name: 'Solana', category: 'Layer 1', score: 92, change: +5.2 },
  { id: 2, name: 'Serum', category: 'DeFi', score: 85, change: +3.7 },
  { id: 3, name: 'Metaplex', category: 'NFT', score: 78, change: +2.1 },
  { id: 4, name: 'Raydium', category: 'DeFi', score: 75, change: -1.2 },
  { id: 5, name: 'Star Atlas', category: 'GameFi', score: 72, change: +4.5 }
]

// 工具函数
function getSignalTypeClass(type: string): string {
  const classes = {
    trend: 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200',
    momentum: 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200',
    reversal: 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
  }
  return classes[type as keyof typeof classes] || 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-200'
}

function getSignalStrengthClass(strength: number): string {
  if (strength >= 80) return 'bg-green-500'
  if (strength >= 60) return 'bg-yellow-500'
  if (strength >= 40) return 'bg-orange-500'
  return 'bg-red-500'
}

function formatTime(date: Date): string {
  return new Intl.DateTimeFormat('zh-CN', {
    hour: '2-digit',
    minute: '2-digit'
  }).format(date)
}

// 初始化图表
function initSignalTrendChart() {
  if (!signalTrendChart.value) return

  // 模拟数据
  const data = [
    { date: '1月20日', signals: 45 },
    { date: '1月21日', signals: 62 },
    { date: '1月22日', signals: 58 },
    { date: '1月23日', signals: 75 },
    { date: '1月24日', signals: 92 },
    { date: '1月25日', signals: 88 },
    { date: '1月26日', signals: 105 }
  ]

  // 清除现有内容
  d3.select(signalTrendChart.value).selectAll('*').remove()

  // 设置尺寸
  const width = signalTrendChart.value.clientWidth
  const height = 250
  const margin = { top: 20, right: 20, bottom: 30, left: 40 }
  const innerWidth = width - margin.left - margin.right
  const innerHeight = height - margin.top - margin.bottom

  // 创建SVG
  const svg = d3.select(signalTrendChart.value)
    .append('svg')
    .attr('width', width)
    .attr('height', height)
    .append('g')
    .attr('transform', `translate(${margin.left},${margin.top})`)

  // 创建比例尺
  const x = d3.scaleBand()
    .domain(data.map(d => d.date))
    .range([0, innerWidth])
    .padding(0.1)

  const y = d3.scaleLinear()
    .domain([0, d3.max(data, d => d.signals) || 100])
    .range([innerHeight, 0])

  // 创建轴
  svg.append('g')
    .attr('transform', `translate(0,${innerHeight})`)
    .call(d3.axisBottom(x))

  svg.append('g')
    .call(d3.axisLeft(y))

  // 创建柱状图
  svg.selectAll('.bar')
    .data(data)
    .enter()
    .append('rect')
    .attr('class', 'bar')
    .attr('x', d => x(d.date) || 0)
    .attr('y', d => y(d.signals))
    .attr('width', x.bandwidth())
    .attr('height', d => innerHeight - y(d.signals))
    .attr('fill', '#3b82f6')

  // 添加标题
  svg.append('text')
    .attr('x', innerWidth / 2)
    .attr('y', -5)
    .attr('text-anchor', 'middle')
    .style('font-size', '12px')
    .style('fill', '#6b7280')
    .text('每日信号数')
}

function initProjectActivityChart() {
  if (!projectActivityChart.value) return

  // 模拟数据
  const data = [
    { date: '1月20日', activity: 65 },
    { date: '1月21日', activity: 72 },
    { date: '1月22日', activity: 68 },
    { date: '1月23日', activity: 81 },
    { date: '1月24日', activity: 76 },
    { date: '1月25日', activity: 85 },
    { date: '1月26日', activity: 90 }
  ]

  // 清除现有内容
  d3.select(projectActivityChart.value).selectAll('*').remove()

  // 设置尺寸
  const width = projectActivityChart.value.clientWidth
  const height = 250
  const margin = { top: 20, right: 20, bottom: 30, left: 40 }
  const innerWidth = width - margin.left - margin.right
  const innerHeight = height - margin.top - margin.bottom

  // 创建SVG
  const svg = d3.select(projectActivityChart.value)
    .append('svg')
    .attr('width', width)
    .attr('height', height)
    .append('g')
    .attr('transform', `translate(${margin.left},${margin.top})`)

  // 创建比例尺
  const x = d3.scaleBand()
    .domain(data.map(d => d.date))
    .range([0, innerWidth])
    .padding(0.1)

  const y = d3.scaleLinear()
    .domain([0, 100])
    .range([innerHeight, 0])

  // 创建轴
  svg.append('g')
    .attr('transform', `translate(0,${innerHeight})`)
    .call(d3.axisBottom(x))

  svg.append('g')
    .call(d3.axisLeft(y))

  // 创建折线图
  const line = d3.line<typeof data[0]>()
    .x(d => x(d.date) || 0)
    .y(d => y(d.activity))
    .curve(d3.curveMonotoneX)

  svg.append('path')
    .datum(data)
    .attr('fill', 'none')
    .attr('stroke', '#8b5cf6')
    .attr('stroke-width', 2)
    .attr('d', line)

  // 添加数据点
  svg.selectAll('.dot')
    .data(data)
    .enter()
    .append('circle')
    .attr('class', 'dot')
    .attr('cx', d => x(d.date) || 0)
    .attr('cy', d => y(d.activity))
    .attr('r', 4)
    .attr('fill', '#8b5cf6')

  // 添加标题
  svg.append('text')
    .attr('x', innerWidth / 2)
    .attr('y', -5)
    .attr('text-anchor', 'middle')
    .style('font-size', '12px')
    .style('fill', '#6b7280')
    .text('项目活跃度')
}

// 挂载时初始化图表
onMounted(() => {
  initSignalTrendChart()
  initProjectActivityChart()

  // 窗口大小变化时重新初始化图表
  window.addEventListener('resize', () => {
    initSignalTrendChart()
    initProjectActivityChart()
  })
})
</script>

